.TodoList {
  margin: 4rem auto;
  padding: 2rem 3rem 3rem;
  max-width: 500px;
  background: #2b2b52;
  color: #fff;
  box-shadow: 2px 6px 8px rgba(#2f363f, 0.7);
  border-radius: 5px;

  ul {
    margin-top: 2.6rem;
    list-style: none;
    word-wrap: wrap;
  }

  h1 {
    font-weight: normal;
    font-size: 2.6rem;
    letter-spacing: 0.05rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    text-transform: uppercase;

    span {
      display: block;
      font-size: 0.8rem;
      margin-bottom: 0.7rem;
      margin-left: 3px;
      margin-top: 0.2rem;
    }
  }
}
