@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&display=swap);
.Todo{display:flex;justify-content:space-between;margin:0 -3rem 4px;padding:1.1rem 3rem;align-items:center;background:rgba(255,255,255,0.1)}.Todo-complete{opacity:0.6;position:relative}.Todo-complete::before{content:"";position:absolute;top:50%;left:-0.5rem;display:block;width:0%;height:2px;background:#fff;-webkit-animation:strikeLine 0.5s ease-out 0s forwards;animation:strikeLine 0.5s ease-out 0s forwards}.Todo-edit{padding:0.3rem 0.8rem;margin-right:0.5rem;cursor:pointer;border:none;background:#8b78e6;color:#f7f1f1;font-size:0.85rem}.Todo-delete{padding:0.3rem 0.8rem;cursor:pointer;border:none;background:#e74292;color:#fff;font-size:0.85rem}.Todo-form{display:flex;justify-content:space-between}.Todo-form input{border:none;background:#f7f1f1;padding:0.6rem 1.5rem;font-size:medium;font-size:initial}.Todo-form button{padding:0 2.3rem;border:none;background:#10a881;color:#fff;font-size:1rem;border-bottom:5px solid inherit;cursor:pointer}.Todo-form button:hover{background:#1bca9b}@-webkit-keyframes strikeLine{to{width:calc(100% + 1rem)}}@keyframes strikeLine{to{width:calc(100% + 1rem)}}@media (max-width: 500px){.Todo-form{flex-wrap:wrap}.Todo-form input{margin-bottom:1rem;width:100%}.Todo-form button{padding:0.55rem 2.3rem}}

.NewTodoForm{margin-top:1rem;display:flex;flex-wrap:wrap}.NewTodoForm label{min-width:100%;margin-bottom:0.5rem;font-size:1.3rem}.NewTodoForm input[type="text"]{flex-grow:1;border:none;background:#f7f1f1;padding:0.6rem 1.5rem;font-size:medium;font-size:initial;margin-bottom:1rem}.NewTodoForm button{padding:0.5rem 1.3rem;border:none;background:#ed64a6;color:#fff5f7;font-size:0.8rem;text-transform:uppercase;cursor:pointer;border-bottom:5px solid #f687b3;box-shadow:1px 1px 1px rgba(0,0,0,0.2);letter-spacing:0.2rem;transition:all 0.3s ease-out}.NewTodoForm button:hover{background:#9f7aea;color:#faf5ff;border-color:#b794f4;letter-spacing:0.15rem}

.TodoList{margin:4rem auto;padding:2rem 3rem 3rem;max-width:500px;background:#2b2b52;color:#fff;box-shadow:2px 6px 8px rgba(47,54,63,0.7);border-radius:5px}.TodoList ul{margin-top:2.6rem;list-style:none;word-wrap:wrap}.TodoList h1{font-weight:normal;font-size:2.6rem;letter-spacing:0.05rem;border-bottom:1px solid rgba(255,255,255,0.3);text-transform:uppercase}.TodoList h1 span{display:block;font-size:0.8rem;margin-bottom:0.7rem;margin-left:3px;margin-top:0.2rem}

*{margin:0;padding:0;box-sizing:border-box}html,body{height:100%;background:linear-gradient(90deg, #30336b, #74b9ff, #6a89cc);font-size:1.1rem;font-family:"Open Sans", sans-serif}

